const ToggleableSection = ({ sections }) => {
    /** When true, shows first section. When false, shows second */
    const [showFirst, setShowFirst] = useState(true);
    const section = showFirst ? sections[0] : sections[1];
    return (jsxs("div", { className: 'toggleable-section', children: [section.component, jsxs("div", { className: 'toggleable-section__toggle-prompt', children: [jsx(Typography, { color: 'secondary', variant: 'body_small', copykey: section.labelCopyKey, children: section.label }), jsx("button", { className: 'toggleable-section__toggle-prompt__button', onClick: () => setShowFirst(!showFirst), children: jsx(Typography, { weight: 'medium', variant: 'body_small', color: 'brand-primary', className: 'toggleable-section__toggle-prompt__button__text', copykey: section.buttonCopyKey, children: section.button }) })] })] }));
};

